import {Component, Input, OnInit} from '@angular/core';
import {FileUploader} from 'ng2-file-upload';
import {File} from 'src/app/main/file/file';
import {User} from '../../home/user';
import {FileService} from '../../service/file.service';

@Component({
  selector: 'app-file',
  templateUrl: './file.component.html',
  styleUrls: ['./file.component.scss']
})
export class FileComponent implements OnInit {
  @Input() user: User;
  @Input() users: Array<User>;
  uploader: FileUploader; // 
  file: File = new File();
  down = 'http://localhost:8080/cl/down?fileId='; // 
  disable = false; // false
  progressbarValue: number; // 
  striped: boolean; // 
  type: string; // 

  constructor(private fileService: FileService) {
    this.progressbarValue = 0;
    this.striped = true;
    this.type = 'danger';
  }

  ngOnInit() {
    // 
    this.uploader = new FileUploader({
      url: `/cl/upload/${this.user.uid}`,
      method: 'POST',
      itemAlias: 'file'
    });
  }

  // 
  selectedFileOnChanged(e: any) {
    // 
    if (e.target.files[0]) {
      this.file.fileName = e.target.files[0].name;
    } else {
      this.file.fileName = '';
    }
    // 
    this.progressbarValue = 0;
    this.type = 'danger';
    this.striped = true;
  }

  // 
  upload() {
    // 
    this.striped = true;
    this.disable = true; // 
    // 
    if (!this.uploader.queue[0]) {
      alert('');
      return;
    } else { // 
      this.uploader.queue[0].upload(); // 
      this.uploader.queue[0].onProgress = (progress) => {// 
        this.progressbarValue = progress;
        if (this.progressbarValue >= 30 && this.progressbarValue < 60) {
          this.type = 'warning';
        } else if (this.progressbarValue > 60 && this.progressbarValue <= 90) {
          this.type = 'info';
        } else {
          this.type = 'success';
        }
      };
      this.uploader.queue[0].onSuccess = (response, status, headers) => {// 
        // 
        if (status === 200) {
          // 
          this.striped = false;
          this.disable = false;
          // 
          this.fileService.showFiles().subscribe((res: any[]) => {
              // res.forEach((user) => {
              //   // 
              //   if (user.uid === this.user.uid) {
              //     this.user = user;
              //   }
              // });
              this.users = res;
            }
          );
          this.file.fileName = '';
          this.uploader.clearQueue();
        } else {
          // 
          alert('');
        }
      };
    }
  }

}
